import React, {useState, useEffect} from 'react';
import cn from 'classnames';
import {Button, Checkbox, Form, Input, Switch, Select} from 'antd';
import add from '../../../common/images/auth/add.svg';
import photo from '../../../common/images/auth/photo.svg';
import {api} from '../../../utils/api';
import Avatar from '../../../utils/avatar';
import {SelectItem} from '../../../utils/supports';

export const SecondStep = ({step, setStep, setFinish, initials, name}) => {
  const [form] = Form.useForm();
  const [src, setSrc] = useState(null)
  const [selectLanguage, setSelectLanguage] = useState(null)
  const [tags, setTags] = useState({})
  const [communication, setCommunication] = useState({})
  const [switchForm, setSwitchForm] = useState(false)
  const [linkText, setLinkText] = useState('')
  const [links, setLinks] = useState([])

  const dropLink = (link) => setLinks(links.filter(l => l !== link))

  const onFinish = values => {
    try {
      api.profileUpdate({...values, avatar: src, selectLanguage, tags, communication, switch: switchForm})
      setStep(3)
    } catch (e) {
    }
    setFinish({...values, avatar: src, selectLanguage, tags, communication, switch: switchForm})
  };

  useEffect(() => {
    window.scrollTo(0, 0)
  }, []);

  return (
    <div className='second-step'>
      <Avatar src={src} setSrc={setSrc} initials={initials} icon={photo}/>
      <h3 className='second-step__name'>{name}</h3>
      <Form
        form={form}
        name='register'
        onFinish={onFinish}
        scrollToFirstError>
        <div className='steps'>
          <div className={cn('step', {'current-step': step === 2})}/>
        </div>

        <Form.Item
          name='language'
          label='Language'>
          <Select className='ant-select-customize-input' onChange={setSelectLanguage} placeholder='Please select a Language'>
            <Select.Option value='english'>English</Select.Option>
            <Select.Option value='russian'>Russian</Select.Option>
          </Select>
        </Form.Item>

        <Form.Item name='university' label='University'>
          <Input placeholder='Название университета'/>
        </Form.Item>


        <Form.Item name='year-of-end' label='Year of end'>
          <Input placeholder='0000'/>
        </Form.Item>

        <Form.Item name='social-media-contacts' label='Social media contacts'>
          <div className='flex-align-center' style={{position: 'relative'}}>
            <Input placeholder='Enter social link' onChange={e => setLinkText(e.target.value)}/>
            <img src={add} alt='add new link' onClick={() => setLinks([...links, linkText])} style={{position: 'absolute', right: '-80px', cursor: 'pointer'}}/>
          </div>
        </Form.Item>

        <div className='links'>
          {!!links.length && links.map((link, i) => <SelectItem key={link + i} option={link} dropSelect={dropLink}/>)}
        </div>
        <div className='mb12'>
          <Form.Item className='border-none' name='tags' label='Tags' onChange={e => setTags({...tags, [e.target.value]: e.target.checked})}>
            <div><Checkbox value='Networking'>Networking</Checkbox></div>
            <div><Checkbox value='Preparing for an interview'>Preparing for an interview</Checkbox></div>
            <div><Checkbox value='I am looking for a team'>I am looking for a team</Checkbox></div>
          </Form.Item>
        </div>
        <Form.Item name='about-me' label='Расскажите о себе'>
          <Input.TextArea placeholder='Свободная форма'/>
        </Form.Item>

        <div className='mb22'>
          <Form.Item className='border-none' onChange={e => setCommunication({...communication, [e.target.value]: e.target.checked})} label='Предпочтительный способ связи'>
            <div><Checkbox>Email</Checkbox></div>
            <div><Checkbox>Phone</Checkbox></div>
            <div><Checkbox>Social media</Checkbox></div>
          </Form.Item>
        </div>
        <Form.Item className='border-none'>
          <div className='search-access'>
            <div className='search-access__title'>Доступен для поиска</div>
            <Switch onChange={setSwitchForm}/>
          </div>
          <div className='search-access__text'>Откройте профиль для поиска, чтобы дать возможность другим участникам платформы просматривать вашу анкету, отправлять вам запрос на
            добавление в друзья и совместно решать кейсы.
          </div>
        </Form.Item>
        <Form.Item className='border-none'>
          <Button className='blue mt30' htmlType='submit' shape='round'>
            Сохранить данные
          </Button>
          <Button className='mt20' htmlType='submit' shape='round'>
            Заполнить позже
          </Button>
        </Form.Item>
      </Form>

    </div>
  );
};



