import React, {useState} from 'react';
import {Switch, Input} from 'antd';
import Star from '../icon/Star';
import {pdf} from '../../common/images/account';
import {closeMimi} from '../../common/images/partners';
import {close} from '../../common/images/materials';
import './index.scss';

export const File = ({title, size}) =>
  <div className='file'>
    <img src={pdf} alt='pdf'/>
    <div className='file__info'>
      <h5 className='file__info-title'>{title}</h5>
      <div className='file__info-size'>Размер файла: {size}</div>
    </div>
  </div>

export const SelectItem = ({option, dropSelect}) =>
  <div className='select-item'>
    <div className='select-item__text'>
      {option.isSearch ? option.search : option}
    </div>
    <div className='select-item__drop' onClick={() => dropSelect(option, option.isSearch && option.search)}>
      <img src={closeMimi} alt='close'/>
    </div>
  </div>

const defPhotos = [
  {
    full: 'https://bucketlistdot.files.wordpress.com/2018/01/5877798-cherry-blossom-pictures.jpg',
    small: 'https://lh3.googleusercontent.com/20AcRFsZgaydNl-DNtCW99UvGnk1VDu7uHvyfRQbmYoqVlbZA-CB2eD_rSIhze3kLpI=w80'
  },
  {
    full: 'http://pm1.narvii.com/6928/ba9a83fe48593f8fb02d19bffb231105b4ca0ec2r1-1024-768v2_uhq.jpg',
    small: 'https://lh3.googleusercontent.com/dqxf2xEf-HlFbjyhtP7-KTn778RgrBvXxTzAuGMGghZQuzfRG66hc3NYdiv10cJvXg=w80'
  },
  {
    full: 'https://bucketlistdot.files.wordpress.com/2018/01/5877798-cherry-blossom-pictures.jpg',
    small: 'https://lh3.googleusercontent.com/20AcRFsZgaydNl-DNtCW99UvGnk1VDu7uHvyfRQbmYoqVlbZA-CB2eD_rSIhze3kLpI=w80'
  }
];

export const CaseInfo = ({text, files, photos, title}) => {
  const [showPopup, setShowPopup] = useState(false)
  const [currentImage, setCurrentImage] = useState(null)
  const onShowImage = (img) => {
    setShowPopup(true)
    setCurrentImage(img)
  }

  return <>
    <div className='case-text'>
      <p>{text}</p>
    </div>
    <div className='case-files'>
      <div className='case-files__title'>{title}</div>
      <div className='case-files__main'>
        {files.map(({title, size}, i) => <File key={title + i} title={title} size={size}/>)}
      </div>
    </div>
    {!photos && <div className='case-files'>
      <div className='case-files__title'>Изображения</div>
      <div className='case-files__main photo-files'>
        {defPhotos.map(({full, small}, i) => <img key={small + i} onClick={() => onShowImage(full)} className='photo-file' src={small} alt='загруженные изображения'/>)}
      </div>
    </div>}
    {showPopup && <Popup image={currentImage} setShowPopup={setShowPopup}/>}
  </>
}

export const TextArea = ({text, setText}) =>
  <div className='textarea'>
    <div className='textarea__title'>Ваш комментарий</div>
    <Input.TextArea placeholder='Текст комментария' value={text} onInput={e => setText(e.target.value)}/>
    <div className='textarea__words'>Всего символов {text.length} / 5000</div>
  </div>

export const Stars = ({stars}) => {
  return Array(7).fill(0).map((_, i) => i < stars ? <Star key={i} fill='#feda00'/> : <Star key={i}/>)
}
export const SearchAccess = () =>
  <>
    <div className='search-access'>
      <div className='search-access__title'>Доступен для поиска</div>
      <Switch/>
    </div>
    <div className='search-access__text'>
      Откройте профиль для поиска,
      чтобы дать возможность другим участникам платформы просматривать вашу анкету,
      отправлять вам запрос на добавление в друзья и совместно решать кейсы.
    </div>
  </>

export const Popup = ({image, setShowPopup}) => {
  return (
    <div className='popup'>
      <div className='popup-container'>
        <div className='popup__close' onClick={() => setShowPopup(false)}>
          <img src={close} alt='close'/>
        </div>
        <div className='popup__video'>
          <div className='popup__video-block'>
            {image ? <img src={image} alt='loaded image'/>
              : <iframe className='popup__video-iframe' width='1000' height='555' src='https://www.youtube.com/embed/d84nAXalLck' frameBorder='0'
                        allow='accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture' allowFullScreen/>
            }
          </div>

        </div>
      </div>
    </div>
  )
}
